<template>
  <el-card class="box-card">
    <el-form ref="dataForm" :model="param" :rules="rule" label-width="80px">
      <el-form-item label="longName" prop="longName">
        <el-input v-model="param.longName" placeholder="longName"></el-input>
      </el-form-item>
      <el-form-item label="rating" prop="rating">
        <el-input type="number" v-model="param.rating" placeholder="rating"></el-input>
      </el-form-item>
      <el-form-item label="spent" prop="spent">
        <el-input type="number" v-model="param.spent" placeholder="spent"></el-input>
      </el-form-item>
      <el-form-item label="location" prop="location">
        <el-input v-model="param.location" placeholder="location"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">confirm</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        longName: '',
        rating: '',
        spent: '',
        location: '',
      },
      rule: {
        longName: [{required: true, message: "please fill in longName", trigger: 'blur'}],
        rating: [{required: true, message: "please fill in rating", trigger: 'blur'}],
        spent: [{required: true, message: "please fill in spent", trigger: 'blur'}],
        location: [{required: true, message: "please fill in location", trigger: 'blur'}],
      }
    }
  },
  methods: {
    onSubmit() {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$axios.post('/restaurants/create', this.param).then(res => {
            if (res.success) {
              this.$message({
                message: 'successfully added',
                type: 'success'
              });
              this.$router.back();
            } else {
              this.$message.error(res.message);
            }
          })
        }
      });
    }
  }
}
</script>

